import SpotifyPlayer from 'react-spotify-web-playback';
import {Component} from 'react'
import {PlayCircleOutline} from '@material-ui/icons';
import './../Styling/Top.css';


const format_songs=(term)=>{
    const key = 'tracks'.concat('_', String(term));
    const data = JSON.parse(localStorage.getItem(key))
    var titles = []
    var artists = []
    var img_url = []
    var album = []
    var popularity = []
    var uri = []
    for(var i in data){
        var track = data[i]
        titles.push(track['name'])
        artists.push(track['artist'])
        img_url.push(track['img'])
        album.push(track['album'])
        popularity.push(track['popularity'])
        uri.push(track['uri'])
    }
    return {'titles':titles, 'artists':artists, 'img_url':img_url, 'album':album,
        'popularity':popularity, 'uri':uri}
}

class WebPlayer extends Component {
    constructor(props) {
    super(props);
    this.state = {
      table_data: null,
      uri_data: null,
      idx: 0,
      play: false,
      previously_clicked: null
        };
    }
    onClickk = (e) =>{
        var id = Number(e.currentTarget.id)
        if(this.state.previously_clicked){
            this.state.previously_clicked.style.color = 'white'
        }
        e.target.style.color = 'green'
        this.setState({idx: id,
            play: true,
            previously_clicked: e.target})
        console.log(id)
    }

    componentDidMount(){
        console.log(this.props.term)
        var data = format_songs(this.props.term)
        var listData = []
        for(var i = 0; i<data['titles'].length;i++){
            listData.push(
            (<tr key={i} className='th1' style={{ textAlign: 'left' }}>
                <td className='td1'><button className= 'play_btn' id={String(i)} onClick={this.onClickk}>
                    <PlayCircleOutline/>
                </button></td>
                <td className='td1'>{data['titles'][i]}</td>
                <td className='td1'>{data['artists'][i]}</td>
                <td className='td1'>{data['album'][i]}</td>
                <td className='td1'>{data['popularity'][i]}</td>
            </tr>
            ))
        }
        this.setState({table_data: listData})
        this.setState({uri_data: data['uri']})
    }

    render(){
    if(this.state.uri_data){
        var uris = this.state.uri_data
        var idx = this.state.idx
    }
    return (
        <div className= 'player_container'>
        <table className='table1' style={{ alignItems: 'center' }}>
            <tbody>
            <tr className='tr1'>
            <th className='th1' style={{ textAlign: 'left' }}>
            </th>
                <th className='th1' style={{ textAlign: 'left' }}>Title
            </th>
                <th className='th1' style={{ textAlign: 'left' }}>Artist
            </th>
                <th className='th1' style={{ textAlign: 'left' }}>Album
            </th>
                <th className='th1' style={{ textAlign: 'left' }}>Popularity
            </th>
            </tr>
            {this.state.table_data}
        </tbody>
    </table>
     
     <div className='web_player'>
     <SpotifyPlayer
         token={localStorage.getItem('token')}
         uris={uris}
         offset = {idx}
         play={this.state.play}
         styles={{
            activeColor: '#fff',
            bgColor: '#333',
            color: '#fff',
            loaderColor: '#fff',
            sliderColor: '#1cb954',
            trackArtistColor: '#ccc',
            trackNameColor: '#fff',
          }}
     />
     </div>
     </div>
    )
    }
}

export default WebPlayer