import Dropdown from 'react-bootstrap/Dropdown'
import DropdownButton from 'react-bootstrap/DropdownButton';
import React, {useState} from "react";
import Webplayer from './Webplayer.js';
import './../Styling/Top.css';

const DropDown = ({on_Select})=>{
    return (
        <DropdownButton
        variant="success"
        title="Top Songs"
        id="dropdown-menu-align-right"
        onSelect={on_Select}
          >
                <Dropdown.Item eventKey="short">Short Term</Dropdown.Item>
                <Dropdown.Item eventKey="medium">Medium Term</Dropdown.Item>
                <Dropdown.Item eventKey="long">All Time</Dropdown.Item>
        </DropdownButton>
    )
}

const Display_songs= ({term}) =>{
    const key = 'tracks'.concat('_', String(term));
    const data = JSON.parse(localStorage.getItem(key))
    var titles = []
    var artists = []
    var img_url = []
    var album = []
    var popularity = []
    for(var i in data){
        var track = data[i]
        titles.push(track['name'])
        artists.push(track['artist'])
        img_url.push(track['img'])
        album.push(track['album'])
        popularity.push(track['popularity'])
    }
    var listData = img_url.map((item, index) =>
        <ul class="img-item">
            <img className="albums" key={index} src={item}/>
            <div class="details">
            <h3><a class="title">{titles[index]}</a></h3>
            <p class="artist">{artists[index]}</p>
            </div>
        </ul>);
    return(
        <div className='row'>
            <div class='column'>{listData.slice(0,10)}</div>
            <div class='column'>{listData.slice(10,20)}</div>
            <div class='column'>{listData.slice(20,30)}</div>
            <div class='column'>{listData.slice(30,40)}</div>
            <div class='column'>{listData.slice(40,50)}</div>
        </div>)
    }

const Dashboard= () =>{
    const [term,setTerm]= useState('short');
    const [view,setView]= useState('img');

    const handleSelect=(e)=>{
        setTerm(e)
        setView('img')
      }

    const handleClick=(e)=>{
        console.log('clicked')
        setView('table')
    }
    return(
        <div className='dash'>
        <div className = 'menu'>
            <DropDown className='drop' on_Select = {handleSelect}/>
            <button class="btn btn-outline-success" onClick = {handleClick} type="submit">Web Player</button>
            <button class="btn btn-outline-success" type="submit">To Playlist</button>
         </div>
         <div className = 'contents'>
             {view === 'img'?
                <Display_songs term = {term}/>:
                <Webplayer term = {term}/>
            }
         </div>
        </div>
    )
}

export default Dashboard